.pro-cons-table {
    padding: 20px;
    box-sizing: border-box;
    background-color: #F6F6F6;
    display: flex;
    font-family: 'Roboto';
    .one-half {
        width: 50%;
        &:first-child {
            padding-right: 10px;
            border-right: 2px solid #e5e5e5;
        }
        h4 {
            margin-top: 0;
            text-align: center;
            text-transform: uppercase;
            margin-bottom: 15px;
            color: #7c284e;
            font-weight: 600;
        }
        ul {
            list-style-type: none;
            padding-left: 0;
            li {
                padding-left: 40px;
                margin-bottom: 5px;
                &::before {
                    content: "+";
                    color: #0ab154;
                    font-weight: 700;
                    font-size: 24px;
                    margin-left: -25px;
                    float: left;
                }
            }
        }
        &:last-child {
            ul {
                padding-left: 40px;
                li {
                    padding-left: 20px;
                    &::before {
                        content: "-";
                        color: #f01818;
                        font-weight: 700;
                        font-size: 30px;
                        margin-left: -25px;
                        float: left;
                    }
                }
            }
        }
    }
}
@media (max-width: 991px) {
    .pro-cons-table {
        .one-half {
            border: none;
            &:first-child {
                border-right: none;
            }
        }
    }
}
@media (max-width: 767px) {
    .pro-cons-table {
        flex-direction: column;
        .one-half {
            width: 100%;
            padding: 0;
            &:first-child {
                border: none;
            }
            &:last-child {
                ul {
                    padding-left: 0;
                }
            }
            ul li {
                padding-left: 20px;
            }
        }
    }
}